// ÂÆö‰πâË°®ÊÉÖÂåÖ
const emojis = [
    'üòÄ', 'üòÇ', 'üòç', 'üòé', 'üò≠', 'üò°', 'üòú', 'üòá', 'üò±', 'üòà',
    'üëç', 'üëé', 'ü•∫', 'üò¢', 'üò≥', 'üòã', 'üò¨', 'üòë', 'ü•≥', 'üò¥'
];

const generateMessageWithEmojis = () => {
    // ÁîüÊàê 10 Âà∞ 20 ‰∏™Ë°®ÊÉÖÂåÖ
    const numberOfEmojis = Mock.Random.integer(5, 10);
    const emojisString = Array.from({ length: numberOfEmojis }, () => Mock.Random.pick(emojis)).join(' ');
    return emojisString;
};

const generateChatData = (num) => {
    // ÂΩì num Â∞è‰∫éÁ≠â‰∫é 0 Êó∂ËøîÂõûÁ©∫Êï∞ÁªÑ
    if (num <= 0) {
        return [];
    }

    // Á°Æ‰øù Mock.mock ËÉΩÊ≠£Á°ÆÁîüÊàêÂçï‰∏™ÂØπË±°ÁöÑÊï∞ÁªÑ
    const result = Mock.mock({
        [`chats|${num}`]: [
            {
                'avatar': '@image("100x100", "#ffccff", "#000000", "Avatar")', // ÁîüÊàêÂ§¥ÂÉèÂõæÁâáÁöÑ URL
                'nickname': '@cname', // ‰∏≠ÊñáÂêçÂ≠ó
                'message': () => generateMessageWithEmojis() // ÁîüÊàêÂ∏¶ÊúâÂ§ö‰∏™Ë°®ÊÉÖÂåÖÁöÑÂèëË®Ä
            }
        ]
    });

    // Ê£ÄÊü•ËøîÂõûÁªìÊûúÊòØÂê¶ÊòØÊï∞ÁªÑÔºåÂ¶ÇÊûú‰∏çÊòØÂàôÂ§ÑÁêÜ‰∏∫Êï∞ÁªÑ
    return result.chats instanceof Array ? result.chats : [result.chats];
};

const chatData = generateChatData(200);

